.transformLeft {
  // transform: translateX(-450px)
  transform: translateX(-75px);
  opacity: 0; }


.body-menu-opened {
  .transformLeft {
    transform: translateX(5px);
    opacity: 1; } }


.body-menu-close {
  .transformLeft {
    transform: translateX(-75px);
    opacity: 0; } }

.transformUp {
  transform: translateY(-10px);
  opacity: 0; }

.body-menu-opened {
  .transformUp {
    transform: translateY(0px);
    opacity: 1; } }


.body-menu-close {
  .transformUp {
    transform: translateY(-10px);
    opacity: 0; } }
