

/* Размер шрифта будет увеличиваться в зависимости от размера экрана */

@mixin dynamicText($minSizePercent, $maxSize, $minSize, $minWidthPx, $maxWidth, $minWidth) {
  font-size: calc(#{$minSizePercent} + (#{$maxSize} - #{$minSize}) * (100vw - #{$minWidthPx}) / (#{$maxWidth} - #{ $minWidth})); }

// $minSizePercent - минимальное значение шрифта в %
// $maxSize        - максимальный размер шрифта
// $minSize        - минимальный расмер шрифта
// $minWidthPx     - минимальная ширина вьюпорта в px
// $maxWidth       - максимальная ширина
// $minWidth       - минимальная ширина

//  Например

// .header
// 	font-size: 18px

// 	@media (min-width: 1500px)
