/** Ширина контейнера **/
@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  margin: 0 auto; }


/* Заголовок h2 не будет отображаться в блоке section */
@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
  @content; }

/* как подключить миксин: @include visually-hidden() */


/*Переводит px в rem  */
@function rem($size, $basicSize: 16px) {
  @return $size / $basicSize#{rem}; }
/* font-size: rem(24px);
   line-height: rem(28px); */


/*Переводит px в em  */
@function em($px, $base: 14px) {    //Здесь меняем базовый размер шрифта
  @return ($px / $base) * 1em; }
/* font-size: em(24px);
   line-height: em(28px); */

