@import "fonts";
@import "libs";
@import "vars";
// @import "normalize"   /** Подключить файл сброса стилей **/
@import "_mixins/mixins";
@import "_mixins/mixinss";

@import "blocks/header";
@import "blocks/hamburger";
@import "blocks/content";
@import "blocks/animation";


*,
::before,
::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0; }


body {

  background-image: url(../images/hero.jpg);
  background-color: rgba(#201a1f, .6);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  font-family: $default-font;
  overflow: hidden;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#201a1f, .6);
    transition: all 600ms ease;
    z-index: -1; } }

.body-menu-opened {
  body::before {
    background-color: rgba(#201a1f, .9); } }
.body-menu-close {
  body::before {
    background-color: rgba(#201a1f, .6); } }



li {
  list-style: none; }

a {
  text-decoration: none; }






