

.content {
  position: fixed;
  top: 0;
  bottom: 0;
  display: flex;
  max-width: 100%;
  width: 100%;
  &__logo {
    margin-top: 2em;
    margin-left: 2em;
    width: 150px;
    height: auto;
    img {
      width: 100%; } }
  &__left {
    flex: 70% 0 0;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    transform: translateX(-100%);
    transition: all 800ms ease; }

  &__right {
    flex: 30% 0 0;
    background-color: #f05a5a;
    transform: translateX(100%);
    transition: all 800ms ease;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 18px;
    padding-left: 55px;
    line-height: 32px;
    ul {
      margin-top: 1em; }
    a {
      color: white;
      transition: all 300ms ease;
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: -28px;
        width: 0px;
        height: 5px;
        background-color: #f05a5a;
        transition: all 300ms ease; }
      &:hover {
        text-decoration: underline; } } }
  &__menu {
    margin-top: 18em;
    margin-left: 4em;
    li {
      a {
        font-size: 2.5em;
        line-height: 52px;
        font-weight: 500;
        color: #1b1b1b;
        transition: all 300ms ease;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: -28px;
          width: 0px;
          height: 5px;
          background-color: #f05a5a;
          transition: all 300ms ease; }
        &:hover {
          color: #f05a5a;
          margin-left: 0.5em;
          &::before {
            width: 25px;
            left: -52px; } } } } } }




.body-menu-opened {
  .content__left {
    transform: translateX(0%); }
  .content__right {
    transform: translateX(0%); }
  .hamburger {
    transform: translateX(-570%); } }

.body-menu-close {
  .content__left {
    transform: translateX(-100%);
    transition-delay: 1200ms; }
  .content__right {
    transform: translateX(100%);
    transition-delay: 1200ms; }
  .hamburger {
    transform: translateX(0%);
 } }    // transition-delay: 600ms

